.note-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: 45px;
  left: 0;
  z-index: 1;

  min-width: 220px;
  height: 170px;
  padding: 10px;
  border: none;
  overflow-y: auto;

  color: #3a4f7a;
  font-size: 20px;
  line-height: 1.5;
  text-align: left;

  background-color: #f7eca0;
  box-shadow: 0px 0px 25px -5px #718b93;
  border-top: 7px solid #3a4f7a;

  font-family: 'Special Elite', cursive;

  cursor: auto;
}

.note-card::-webkit-scrollbar,
.note-card::-webkit-scrollbar-thumb {
  width: 12px;
  border-radius: 6px;
  border: 4px solid #f7eca0;
}

.note-card::-webkit-scrollbar-thumb {
  box-shadow: none;
  background: #969faf;
}

.note-card-controls {
  display: flex;
  justify-content: space-around;
  margin-bottom: 10px;
}
